<template>
    <div class="actions">
        <top-info @time="getTime"></top-info>
        <!-- card-list -->
        <div class="cards" v-if="info">
            <div class="item">
                <i class="iconfont icon-duihuazixun"></i>
                <div class="count">
                    <p class="text">查看产品</p>
                    <p class="num"><span class="big">{{info['6']}}</span>次</p>
                </div>
            </div>
            <div class="item">
                <i class="iconfont icon-phone"></i>
                <div class="count">
                    <p class="text">查看品类</p>
                    <p class="num"><span class="big">{{info['1']}}</span>次</p>
                </div>
            </div>
        </div>
        <div class="cards" v-if="info">
            <div class="item">
                <i class="iconfont icon-sousuo"></i>
                <div class="count">
                    <p class="text">使用搜索</p>
                    <p class="num"><span class="big">{{info['11']}}</span>次</p>
                </div>
            </div>
            <div class="item" v-if="info">
                <i class="iconfont icon-13zixun"></i>
                <div class="count">
                    <p class="text">在线咨询</p>
                    <p class="num"><span class="big">{{info['9']}}</span>次</p>
                </div>
            </div>
        </div>
        <cell-group v-if="info">
            <cell :value="info['2']+'次'">
                <template slot="title">
                    <span class="van-cell-text">查看店铺</span>
                </template>
            </cell>
            <cell :value="info['10']+'次'">
                <template slot="title">
                    <span class="van-cell-text">查看客服电话</span>
                </template>
            </cell>
            <cell :value="info['3']+'次'">
                <template slot="title">
                    <span class="van-cell-text">查看名片</span>
                </template>
            </cell>
            <cell :value="info['12']+'次'">
                <template slot="title">
                    <span class="van-cell-text">转发名片</span>
                </template>
            </cell>
            <cell :value="info['1']+'次'">
                <template slot="title">
                    <span class="van-cell-text">查看商城首页</span>
                </template>
            </cell>
            <cell :value="info['7']+'次'">
                <template slot="title">
                    <span class="van-cell-text">点击立即预定</span>
                </template>
            </cell>
            <cell :value="info['8']+'次'">
                <template slot="title">
                    <span class="van-cell-text">点击到店咨询</span>
                </template>
            </cell>
        </cell-group>
    </div>
</template>

<script>
import TopInfo from '@/components/TopInfo/TopInfo';
import { Cell, CellGroup } from 'vant';
import { getActionInfo } from '../../server/data-tracking';
import { getItem } from '../../util/util';
export default {
    data () {
       return {
           info     : null,
           cardId   : 0,
           startday : '',
           endday   : ''
       };
    },
    created() {
        if(getItem('cardId')) {
            this.cardId = getItem('cardId');
        }
        this._getActionInfo();
    },
    methods: {
        async _getActionInfo() {
            try {
                let res = await getActionInfo({cardid: this.cardId, startday: this.startday, endday: this.endday});
                if(res.data.status === 1) {
                    this.info = res.data.info;
                } 
            } catch (error) {
                
            }
        },
        getTime(day) {
            this.info       = null;
            this.startday   = day.startday;
            this.endday     = day.endday;
            this._getActionInfo();
        }
    },
    components: {
        TopInfo,
        Cell,
        CellGroup
    }
}
</script>

<style lang='less' scoped>
@import url(./index.less);
</style>


